.tarefas {
  margin-top: 30px;

}

.tarefas li {
  display: flex;
  justify-content: space-between;
  list-style: none;
  padding: 10px;
  transition: all 300ms ease-in-out;
}

.tarefas li .edit {
  margin-right: 15px;
  color: #51c5de;
  cursor: pointer;
  transition: all 300ms ease-in-out;
}

.tarefas li .delete {
  margin-right: 15px;
  color: #f04c64;
  cursor: pointer;
  transition: all 300ms ease-in-out;
}

.tarefas li:hover {
  background-color: #51c5de;
}

.tarefas li:hover .delete,
.tarefas li:hover .edit {
  color: #000;
}

.tarefas li span {
  display: flex;
  align-items: center;
}
